<template lang="pug">
.home
  .subtext Created @dora
  .pianoWrapper
    //- h1 {{dribbble}}
    .view
    piano(:dribbble="dribbble" :oscillator="oscillator" :pitch="pitch" ref="piano")
  .optWrapper
    .title oscillator
    .oscillators 
      .oscillator(v-for="os,index in oscillators" :class="{active: oscillatorsClick==index}" @click="oscillatorMode(index)") {{os}}

    .title OCT {{pitch}}
    .oscillators 
      .oscillator(@click="octMode(false)") OCT-
      .oscillator(@click="octMode(true)") OCT+
    .oscillators
      .oscillator(@click="stopplaying").btn {{playState}}

</template>

<script>
// @ is an alias to /src
import piano from "../components/svg/piano";
import lottie from "lottie-web";

export default {
  name: "Home",
  components: { piano },
  data() {
    return {
      dribbble: [],
      oscillators: ["sine", "triangle", "square", "sawtooth"],
      oscillator: "sine",
      oscillatorsClick: 0,
      pitch: 4,
      playState: "Play",
      lottieController: "",
    };
  },
  mounted() {
    this.loadLottie();
  },
  methods: {
    loadLottie() {
      this.lottieController = lottie.loadAnimation({
        container: document.querySelectorAll(".view")[0], // the dom element that will contain the animation
        renderer: "svg",
        loop: true,
        autoplay: true,
        animationData: require("../assets/bodymovin/data2.json"), // the path to the animation json
      });
      this.lottieController.playSegments([0, 16], true);
    },
    stopplaying() {
      lottie.play();
      this.$refs.piano.stop();
      this.playState == "Play"
        ? this.lottieController.playSegments([18, 37], true)
        : this.lottieController.pause();
      this.playState = this.playState == "Play" ? "Stop" : "Play";
    },
    oscillatorMode(index) {
      console.log(index);
      this.oscillator = this.oscillators[index];
      this.oscillatorsClick = index;
    },
    octMode(plus) {
      if (plus) {
        this.pitch++;
      } else {
        this.pitch--;
      }
    },
  },
};
</script>
