            rect#2(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='49' y='2' width='43' height='178' rx='4')
            rect#3(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='96' y='2' width='43' height='178' rx='4')
            rect#4(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='143' y='2' width='43' height='178' rx='4')
            rect#5(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='190' y='2' width='43' height='178' rx='4')
            rect#6(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='237' y='2' width='43' height='178' rx='4')
            rect#7(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='284' y='2' width='43' height='178' rx='4')
            rect#8(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='331' y='2' width='43' height='178' rx='4')
            rect#9(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='378' y='2' width='43' height='178' rx='4')
            rect#10(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='425' y='2' width='43' height='178' rx='4')
            rect#11(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='472' y='2' width='43' height='178' rx='4')
            rect#12(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='519' y='2' width='43' height='178' rx='4')
            rect#13(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='566' y='2' width='43' height='178' rx='4')
            rect#14(stroke='#000000' stroke-width='4' fill='#FFFFFF' x='613' y='2' width='43' height='178' rx='4')
            rect(id='1#' fill='#000000' x='30' y='0' width='35' height='102' rx='4')
            rect(id='2#' fill='#000000' x='77' y='0' width='35' height='102' rx='4')
            rect(id='4#' fill='#000000' x='171' y='0' width='35' height='102' rx='4')
            rect(id='5#' fill='#000000' x='218' y='0' width='35' height='102' rx='4')
            rect(id='6#' fill='#000000' x='265' y='0' width='35' height='102' rx='4')
            rect(id='8#' fill='#000000' x='359' y='0' width='35' height='102' rx='4')
            rect(id='9#' fill='#000000' x='406' y='0' width='35' height='102' rx='4')
            rect(id='11#' fill='#000000' x='500' y='0' width='35' height='102' rx='4')
            rect(id='12#' fill='#000000' x='547' y='0' width='35' height='102' rx='4')
            rect(id='13#' fill='#000000' x='594' y='0' width='35' height='102' rx='4')
</template>

<script>
import Tone from "tone";
// import * as dat from "dat.gui";

export default {
  name: "piano",
  props: {
    dribbble: {
      type: Array,
      required: true,
    },
    oscillator: {
      type: String,
      required: true,
    },
    pitch: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      keyController: "",
      text: {
        message: "triangle1",
      },
      synthPart: "",
    };
  },
  mounted() {
    this.keyController = new Tone.PolySynth(6, Tone.synth).toMaster();
    this.keyController.set({
      oscillator: {
        type: `${this.oscillator}`,
        frequency: 40,
        detune: 0,
        phase: 0,
      },
      envelope: {
        attack: 0.025,
        decay: 0.25,
        sustain: 0.28,
        release: 0.2,
      },
    });

    this.register_key();
    this.registerPiano();
    // var gui = new dat.GUI();
    // let listen1 = gui.add(this.text, "message");
    // listen1.onChange(function() {
    //   vm.keyController.set({
    //     oscillator: {
    //       type: `${vm.text.message}`,
    //     },
    //   });
    // });
  },

  methods: {
    registerPiano() {
      if (this.synthPart != "") {
        console.log("reset");
        this.synthPart.dispose();
      }
      // this.keyController.triggerAttackRelease(["C4", "E4", "G4"], "4n");
      // this.keyController = new Tone.PolySynth(6, synth).toMaster();
      // create a synth
      let vm = this;
      // const synth = new Tone.MembraneSynth().toMaster();
      // create an array of notes to be played
      const notes = [
        "1",
        "2",
        "3",
        "1",
        "5",
        null,
        "3",
        "2",
        "5",
        null,
        "2",
        "1",
        "6.",
        "3",
        "1",
        "7.",
        null,
        "7.",
        "6.",
        null,
        "7.",
        "1",
        "2",
        "5.",
        "1",
        null,
        "2",
        "3",
        "4",
        "4",
        "3",
        "2",
        "1",
        "2",
        null,
        "1",
        "2",
        "3",
        "1",
        "5",
        null,
        "3",
        "2",
        "5",
        null,
        "2",
        "1",
        "6.",
        "6.",
        "7.",
        "1",
        "5.",
        null,
        null,
        "5.",
        "6.",
        "7.",
        "1",
        "2",
        "5.",
        "1",
        "2",
        "3",
        "4",
        null,
        "4",
        "3",
        "2",
        "1",
        "1",
        null,
        null,
      ];
      let notesT = notes.map((value) => {
        return vm.pianoConvert(value);
      });
      // create a new sequence with the synth and notes
      this.synthPart = new Tone.Sequence(
        function(time, note) {
          vm.keyController.triggerAttackRelease(note, "10hz", time);
        },
        notesT,
        "4n"
      );
      // Setup the synth to be ready to play on beat 1
      this.synthPart.start();
      this.synthPart.humanize = true;
      // Note that if you pass a time into the start method
      // you can specify when the synth part starts
      // e.g. .start('8n') will start after 1 eighth note
      // start the transport which controls the main timeline
    },
    stop() {
      Tone.Transport.toggle();
    },
    register_key() {
      var vm = this;
      let keys = document.querySelectorAll("rect");
      keys.forEach((value) => {
        value.addEventListener("mousedown", (t) => {
          vm.keyController.triggerAttack(vm.pianoConvert(t.target.id));
        });
        value.addEventListener("mouseup", () => {
          vm.keyController.releaseAll();
        });
      });
      document.addEventListener("keyup", (e) => {
        let key = e.key.toUpperCase();
        if (key === "A") {
          let tg = vm.pianoConvert("1");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        } else if (key === "S") {
          let tg = vm.pianoConvert("2");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        } else if (key === "D") {
          let tg = vm.pianoConvert("3");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        } else if (key === "F") {
          let tg = vm.pianoConvert("4");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        } else if (key === "G") {
          let tg = vm.pianoConvert("5");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        } else if (key === "H") {
          let tg = vm.pianoConvert("6");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        } else if (key === "J") {
          let tg = vm.pianoConvert("7");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        } else if (key === "K") {
          let tg = vm.pianoConvert("8");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        } else if (key === "L") {
          let tg = vm.pianoConvert("9");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        } else if (key === ";") {
          let tg = vm.pianoConvert("10");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        } else if (key === "'") {
          let tg = vm.pianoConvert("11");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        } else if (key === "S") {
          let tg = vm.pianoConvert("12");
          vm.dribbble.splice(vm.dribbble.indexOf(tg), 1);
          vm.keyController.triggerRelease(tg);
        }
        if (vm.dribbble.length == 0) {
          vm.keyController.releaseAll();
        }
      });
      document.addEventListener("keydown", (e) => {
        let key = e.key.toUpperCase();
        let triggerSound = false;
        if (key === "A") {
          if (vm.dribbble.indexOf(vm.pianoConvert("1")) == -1) {
            vm.dribbble.push(vm.pianoConvert("1"));
            triggerSound = true;
          }
        } else if (key === "S") {
          if (vm.dribbble.indexOf(vm.pianoConvert("2")) == -1) {
            vm.dribbble.push(vm.pianoConvert("2"));
            triggerSound = true;
          }
        } else if (key === "D") {
          if (vm.dribbble.indexOf(vm.pianoConvert("3")) == -1) {
            vm.dribbble.push(vm.pianoConvert("3"));
            triggerSound = true;
          }
        } else if (key === "F") {
          if (vm.dribbble.indexOf(vm.pianoConvert("4")) == -1) {
            vm.dribbble.push(vm.pianoConvert("4"));
            triggerSound = true;
          }
        } else if (key === "G") {
          if (vm.dribbble.indexOf(vm.pianoConvert("5")) == -1) {
            vm.dribbble.push(vm.pianoConvert("5"));
            triggerSound = true;
          }
        } else if (key === "H") {
          if (vm.dribbble.indexOf(vm.pianoConvert("6")) == -1) {
            vm.dribbble.push(vm.pianoConvert("6"));
            triggerSound = true;
          }
        } else if (key === "J") {
          if (vm.dribbble.indexOf(vm.pianoConvert("7")) == -1) {
            vm.dribbble.push(vm.pianoConvert("7"));
            triggerSound = true;
          }
        } else if (key === "K") {
          if (vm.dribbble.indexOf(vm.pianoConvert("8")) == -1) {
            vm.dribbble.push(vm.pianoConvert("8"));
            triggerSound = true;
          }
        } else if (key === "L") {
          if (vm.dribbble.indexOf(vm.pianoConvert("9")) == -1) {
            vm.dribbble.push(vm.pianoConvert("9"));
            triggerSound = true;
          }
        } else if (key === ";") {
          if (vm.dribbble.indexOf(vm.pianoConvert("10")) == -1) {
            vm.dribbble.push(vm.pianoConvert("10"));
            triggerSound = true;
          }
        } else if (key === "'") {
          if (vm.dribbble.indexOf(vm.pianoConvert("11")) == -1) {
            vm.dribbble.push(vm.pianoConvert("11"));
            triggerSound = true;
          }
        } else if (key === "S") {
          if (vm.dribbble.indexOf(vm.pianoConvert("12")) == -1) {
            vm.dribbble.push(vm.pianoConvert("12"));
            triggerSound = true;
          }
        }
        if (triggerSound) {
          vm.keyController.triggerAttack(vm.dribbble);
        }
      });
    },
    pianoConvert(t) {
      if (t === null) {
        return null;
      }
      let temp = t.split("#");
      let sharp = "";
      if (temp[1] != undefined) {
        sharp = "#";
      }
      let temp2 = t.split(".");
      let dowpitch = 0;
      if (temp2[1] != undefined) {
        dowpitch = -1;
      }

      let pitch = this.pitch + dowpitch + Math.floor(temp[0] / 8);
      let mark = String.fromCharCode(
        66 + parseInt(temp[0] % 7 == 0 ? 7 : temp[0] % 7)
      );
      if (mark == "H") {
        mark = "A";
      } else if (mark == "I") {
        mark = "B";
      }

      return mark + sharp + pitch;
    },
  },
  watch: {
    pitch: function() {
      this.registerPiano();
    },
    oscillator: function() {
      this.keyController.set({
        oscillator: {
          type: `${this.oscillator}`,
          frequency: 40,
          detune: 0,
          phase: 0,
        },
        envelope: {
          attack: 0.025,
          decay: 0.25,
          sustain: 0.28,
          release: 0.2,
        },
      });
    },
  },
};
</script>

